body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    text-transform: lowercase;
    font-weight: 700;
    font-size: .9rem;
    letter-spacing: .1rem;
}
.navbar-brand {
    height: 2rem;
    color: #5c564a!important;

}
.navbar-brand:hover {
    color: black;
}
.navbar-nav li {
    padding-right: .7rem; 

}
.navbar-white .navbar-nav .nav-link {
    color: #5c564a;
    padding-top: .8rem;

}
.navbar-white .navbar-nav .nav-link.active,
.navbar-white .navbar-nav .nav-link:hover {
    color: black!important;
}


.mr-auto, .mx-auto {
    margin-right: inherit !important;
}
.nav-link {
    font-size: 20px;
}


h4 {
    color: #5c564a;
    margin-top: 130px;
    text-align: center;
}

.button {
    display: inline-block;
    margin-top: 30px;
    font-size: 24px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: violet;
    border: none;
    border-radius: 10px;
    box-shadow: 0 3px darkgray;
}
.container-home span{
    padding: 15px 15px;
}
@media screen and (max-width: 600px) {
  h4 {
    padding: 5px;
  }
}
@media screen and (max-width: 600px) {
  .input-button {
    padding-bottom: 15px;
  }
}
.container-photo {
    background-color: #dddcdf;
    height: 130%;
    padding-top: 4rem;
    padding-bottom: 5rem;
}
.column {
  -webkit-flex: 25% 1;
          flex: 25% 1;
  max-width: 25%;
}
.column img {
  margin-top: 8px;
  vertical-align: middle;
}
.col-12 a {
    color: #5C564A;
}
.col-12 a:hover {
    color: #98D1D2;
}
@media screen and (max-width: 600px) {
  .column {
    -webkit-flex: 100% 1;
            flex: 100% 1;
    max-width: 100%;
  }
}
@media screen and (max-width: 800px) {
  .column {
    -webkit-flex: 50% 1;
            flex: 50% 1;
    max-width: 50%;
  }
}
img{
  padding-top: 0px;
}

.col-md-4 i {
    color: white;
    font-size: 1.6rem;
    margin: 1.2rem;
    letter-spacing: 1rem;
    margin-bottom: 1rem;
}
.col-md-4 i:hover {
    color: #e6d072;
}
.col-md-6 h6 {
    color: #5c5c5c;
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    padding-left: 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.col-md-6 {
  float: left;
  padding-top: 1rem;
}
.col-md-4 {
  float: right;
}
img {
  height: 100%;
  width: 100%;
}
@media screen and (max-width: 600px) {
  img {
    max-width: 100%;
    height: auto;
  }
}
.white {
   margin-top: 100px;
   text-align: center;
}

.signInButton {
    display: inline-block;
    margin-top: 30px;
    font-size: 24px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: violet;
    border: none;
    border-radius: 10px;
    box-shadow: 0 3px darkgray;
}

.input-field {
    padding: 3px;
}
label {
    padding: 10px;
    width: 10%;
}
input {
    border: none;
    box-shadow: 0 2px violet;
}
@media screen and (max-width: 600px) {
  label {
    width: 100%;
  }
}

